.tooltip {
  min-width: 360px;
  position: absolute;
  z-index: 99;
  margin-top: 30px;
  display: inline-block;
  padding: 20px 70px 2px 15px;
  background-color: #fff;
  border-radius: 6px;
  box-shadow: -1px 2px 40px -1px #a4b0bc;
  &::after {
    content: '';
    display: block;
    position: absolute;
    top: -15px;
    left: 47%;
    width: 0; 
    height: 0; 
    border-bottom: 15px solid #fff;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent
  };

  @media(max-width:540px) {
    position: fixed;
    left: 5px;
    bottom: 15px;
    width: calc(100% - 95px);
    min-width: 0px;
    &::after {
      display: none
    }
  }
}

.tooltip__edit-btn {
  position: absolute;
  right: 10px;
  top: 10px
}

.tooltip__headline {
  margin: 0px;
  overflow: hidden;
  text-overflow: ellipsis
}

.tooltip__paragraph {
  margin-top: 5px
}