.line {
  content: 'a';
  font-size: 0px;
  width: 1px;
  display: block;
  margin-left: 19px;
  margin-top: 5px;
  background-color: #dfe8f4;
  height: calc(100vh * 10);
  position: absolute;
}

.time {
  margin-right: 115px;
  position: relative;
  &::after {
    @extend .line;
  };
}

.time--with-offset {
  &::after {
    @extend .line;
    margin-left: 10px;
  }
}

