.room-group {
  margin-top: 27px;
  &:first-child {
    margin-top: 17px;
  };

  &:last-child {
    padding-bottom: 20px
  };

  @media(max-width: 540px) {
    width: 100%;
    margin-top: 23px;

    &:last-child {
      padding-bottom: 0px
    };
  }
}

.room-group__floor-name {
  margin-bottom: -5px;
  padding-left: 25px;

  @media(max-width: 540px) {
    padding-left: 15px;
    margin-bottom: 20px
  }
}