.calendar {
  position: absolute;
  background-color: #ffffff;
  margin-left: 8px;
  border-radius: 5px;
  border: 1px solid #e9ecef;
  box-shadow: 1px 2px 26px 0px rgba(41, 41, 42, 0.1);
  z-index: 100;
  @media(max-width: 540px) {
    margin: 0px;
    left: 50%;
    transform: translateX(-50%);
  }
}