.event {
  position: absolute;
  height: 30px;
  transition: background-color .15s ease-in-out;
  background-color: #d5dfe9;
  cursor: default;
  @media(max-width: 540px) {
    height: 60px;
    margin-top: -25px;
    background-color: rgba(191, 206, 221, 0.5);
  }
}

.event--selected {
  background-color: #99a9b9;
  @media(max-width: 540px){
    background-color: rgba(133, 152, 171, 0.5)
  }
}