.main {
  width: 100%;
  display: flex;
  @media(max-width: 540px) {
    margin-top: 60px
  }
}

.left-column {
  display: flex;
  flex-direction: column;
  @media(max-width: 540px) {
    width: 50%
  }
}

.hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px; 
  width: 1px; 
  overflow: hidden
}