.room-name {
  padding-left: 25px;
  margin-top: 2px;
  @media(max-width: 540px) {
    width: 100%;
    height: 60px;
    background-color: rgba(255, 255, 255, 0.7);
    padding-left: 15px;
    margin-top: -10px;
  }
}

.room-name__headline {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  @media(max-width: 540px) {
    padding-top: 8px;
    width: 83%
  }
}

.room-name__paragraph { 
  margin-top: -10px;
}