.date-picker {
  display: flex;
  margin-left: 25px;
  align-items: center;
  @media(max-width: 540px) {
    background-color: #ffffff;
    position: absolute;
    width: 100%;
    margin-left: 0px;
    height: 60px;
    margin-top: -60px;
    justify-content: space-between;
    border-bottom: 2px solid #f8f9fa
  }
}

.date-picker__headline {
  padding: 0 15px;
  white-space: nowrap;
  cursor: pointer
}

.date-picker__btn-left {
  @media(max-width:540px) {
    margin-left: 15px
  }
}

.date-picker__btn-right {
  @media(max-width:540px) {
    margin-right: 15px
  }
}