.rooms {
  padding-right: 60px;
  min-height: calc(100vh - 120px);
  border-top: 1px solid #f5f6f8;
  height: 100%;
  margin-bottom: 105px;
  @media(max-width: 540px) {
    padding-right: 0px;
    background-color: #f7f8f9;
    border-top: none;
    margin-top: 46px; 
    margin-bottom: 25px; 
  }
}