.tooltip-participators {
  display: flex;
  align-items: center;
  margin-top: -15px
}

.tooltip-participators__image-wrapper {
  width: 25px;
  height: 25px;
  margin-right: 5px
}

.tooltip-participators__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 100%
}

.tooltip-participators__grey-text {
  color: #8c8d8f
}